import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhEcommerce } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-print',
  templateUrl: './print.component.html',
  styleUrls: ['./print.component.scss']
})
export class PrintComponent implements OnInit {

  root: any;
  loading: boolean = true
  infoShop: any = {}
  constructor(
    private router: Router,
    private vhEcommerce: VhEcommerce,
    private vhComponent: VhComponent,
    private http: HttpClient,
  ) {
    this.root = this.router.getCurrentNavigation().extras.state;
    this.infoShop = this.root.infoShop
  }

  ngOnInit(): void {
    this.root.bill['logo'] = this.renderLogoImage(this.root.bill['shipping_carrier']?.toLowerCase())
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      //lấy các dữ liệu thuộc shipping(lưu ý hàm này nếu k thuộc status proccess được in sẽ trả về lỗi)
      // if (this.root.bill['order_status'] == 'PROCESSED') {
      //   
        
      //   this.vhEcommerce.getShippingDocumentShopee(this.root.bill['order_sn'], this.infoShop['access_token'], this.infoShop['_id']).then((res: any) => {
      //     if (res.error) this.showMessageError(res.error)
      //     // else {
      //     //   this.root.bill['shipping_document_info'] = res.response?.shipping_document_info
      //     //   this.root.bill['code_render'] = this.root.bill.shipping_carrier == 'Shopee Express Instant' ? this.root.bill['order_sn'] : res.response?.shipping_document_info.tracking_number
      //     // }
      //     
      //     this.vhComponent.hideLoading(0)
      //     this.loading = false
      //   }).catch(err => { 
      //   ;this.showMessageError(); this.loading = false; this.vhComponent.hideLoading(0) })
      // } else {
      //   
        this.vhEcommerce.getTrackingNumberShopee(this.root.bill['order_sn'], this.infoShop['access_token'], this.infoShop['_id']).then((result: any) => {
          
          
          this.root.bill['shipping_document_info'] = {}
          if (result.error) this.showMessageError(result.error)
          else
            this.root.bill['shipping_document_info'].tracking_number = result.response?.tracking_number || result.tracking_number
          this.loading = false
          this.vhComponent.hideLoading(0)
          this.root.bill['code_render'] = this.root.bill.shipping_carrier == 'Shopee Express Instant' ? this.root.bill['order_sn'] : this.root.bill['shipping_document_info'].tracking_number
          
        },error=>{
          
        }).catch(err => { this.showMessageError(); this.loading = false;   })  
      // }
    })
      
    
  }
  goBack() {
    this.router.navigate([this.root.path], { state: this.root })
  }
  showMessageError(err?) {
    this.vhComponent.alertMessageDesktop('error', err || 'Xảy ra lỗi khi lấy dữ liệu')
  }
  /**render ra hình ảnh nhà giao hàng */
  renderLogoImage(name) {
    if (name.indexOf('ninja') >= 0) return 'assets/icon/management/ecommerce/ninjavan-express.png'
    else if (name.indexOf('viettel') >= 0) return 'assets/icon/management/ecommerce/viettel-express.png'
    else if (name.indexOf('grab') >= 0) return 'assets/icon/management/ecommerce/shopee-grab.png'
    else if (name.indexOf('best') >= 0) return 'assets/icon/management/ecommerce/best-express.png'
    else if (name.indexOf('giao hàng nhanh') >= 0) return 'assets/icon/management/ecommerce/ghn-express.png'
    else if (name.indexOf('giao hàng tiết kiệm') >= 0) return 'assets/icon/management/ecommerce/giaohangtietkiem-express.png'
    else if (name.indexOf('j&t') >= 0) return 'assets/icon/management/ecommerce/j&t-express.png'
    else return 'assets/icon/management/ecommerce/shopee-express.png'
  }
}
