import { K80Component } from './print/k80/k80.component';
import { PrintComponent } from './print/print.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TiktokComponent } from './tiktok.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import { A4Component } from './print/a4/a4.component';
import { A5Component } from './print/a5/a5.component';
import { A6Component } from './print/a6/a6.component';
import { B6Component } from './print/b6/b6.component';
import { B7Component } from './print/b7/b7.component';

const routes: Routes = [
  {
    path: '',
    component: TiktokComponent
  },
  {
    path: 'print',
    component: PrintComponent
  },
  {
    path: 'affiliate-product',
    loadChildren: () => import('./affiliate-product/affiliate-product.module').then(m => m.AffiliateProductModule)
  },
  {
    path: 'order-status',
    loadChildren: () => import('./order-status/order-status.module').then(m => m.OrderStatusModule)
  },
  {
    path: 'update-selling-price',
    loadChildren: () => import('./update-selling-price/update-selling-price.module').then(m => m.UpdateSellingPriceModule)
  },
  {
    path: 'update-stock',
    loadChildren: () => import('./update-stock/update-stock.module').then(m => m.UpdateStockModule)
  },
  {
    path: 'return-refund',
    loadChildren: () => import('./return-refund/return-refund.module').then(m => m.ReturnRefundModule)
  },
  {
    path: 'issue-invoice',
    loadChildren: () => import('./issue-invoice/issue-invoice.module').then(m => m.IssueInvoiceModule)
  },
  {
    path: 'product-on-shop',
    loadChildren: () => import('./product-on-shop/product-on-shop.module').then(m => m.ProductOnShopModule)
  },
  {
    path: 'control',
    loadChildren: () => import('./control/control.module').then(m => m.ControlModule)
  },
  {
    path: 'auto-sale',
    loadChildren: () => import('./auto-sale/auto-sale.module').then(m => m.AutoSaleModule)
  },
  {
    path:'report',
    loadChildren: () => import('./report/report.module').then(m => m.ReportModule)
  },
  {
    path:'e-invoice-tiktok',
    loadChildren: () => import('./e-invoice-tiktok/e-invoice-tiktok.module').then(m => m.EInvoiceTiktokModule)
  },
  {
    path:'e-invoice-sales365',
    loadChildren: () => import('./e-invoice-sales365/e-invoice-sales365.module').then(m => m.EInvoiceSales365Module)
  }
]

@NgModule({
  declarations: [TiktokComponent,PrintComponent,K80Component, A4Component, A5Component, A6Component, B6Component, B7Component],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    FormsModule,
    IonicModule
  ]
})
export class TiktokModule { }
