import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhEcommerce, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { log } from 'util';

@Component({
  selector: 'app-tiktok',
  templateUrl: './tiktok.component.html',
  styleUrls: ['./tiktok.component.scss']
})
export class TiktokComponent {

  listProduct: Array<any> = [] //danh sách sp kèm them id_shop
  listShop: Array<any> = [] //danh sách sendo đã kết nối
  public listCustomer: any[] = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(customer => {
    if (customer['_id'] != "id_retail") {
      if (customer['debt_enable'] == true) return true
      else return false
    } return false
  });
  customerShow: Array<any> = []
  modalCustomer: any = {
    visible: false,
    loading: false,
    name: '',
    id_customer: '',
    info: {},
    existShop: false
  }
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private vhAlgorithm: VhAlgorithm,
    private vhEcommerce: VhEcommerce
  ) {
    let dataRestore = this.router.getCurrentNavigation().extras.state?.dataRestore
    if (dataRestore && dataRestore.haveUpdate) {
      this.syncData()
    } else this.getData()
  }
  /**Đồng bộ lại dữ liệu khi có thay đổi */
  syncData() {
    Promise.all([
      this.vhComponent.showLoading(this.lang.translate("Syncing product data Tiktok..."), "transparent-loading", undefined, 0, true, 60000),
      this.vhEcommerce.refreshLocalProductListTiktok(this.vhQuerySales.getDefaultBranch()._id)
    ]).then(([refresh, loading]: any) => {
      console.log(loading);

      this.vhComponent.hideLoading(0).then(() => {
        this.getData()
      })
    }).catch(err => { console.error(err); this.vhComponent.hideLoading(0) })
  }
  /**
   * Duyệt qua từng shop, nếu shop nào không có id_customer thì tiến hành thêm tên và thêm id_customer cho shop
   */
  getData() {
    this.listShop = this.vhEcommerce.getlocalShopListTiktok();
    this.customerShow = [...this.listCustomer]
    console.log(this.listShop);
    for (let item of this.listShop) {

      if (!item.data?.id_customer) {
        this.vhQuerySales.getShopPartner(item._id).then((customer: any) => {
          console.log(customer);

          if (customer) {
            this.modalCustomer['id_customer'] = customer['_id']
            this.modalCustomer['existShop'] = true
          }
          this.modalCustomer['info'] = item
          this.modalCustomer['visible'] = true
        })
        break;
      }
    }
  }
  /**
   * Cập nhật shop
   */
  updateInfoTiktok() {
    this.modalCustomer['loading'] = true;
    let data = {
      id_customer: this.modalCustomer['id_customer'],
      address: this.modalCustomer['address'],
      date: new Date().toISOString()
    }
    console.log('id_shop--------', this.modalCustomer['info']._id);
    console.log('data--------', data);
    
    this.vhEcommerce.updateShop(this.modalCustomer['info']._id, data)
      .then((bool: boolean) => {
        console.log(bool);

        if (bool) {
          this.vhComponent.alertMessageDesktop("success", this.lang.translate("Update successfully"))
        }
        else this.vhComponent.alertMessageDesktop("error", this.lang.translate("Update failed"))
        this.modalCustomer['loading'] = false;
        this.modalCustomer['visible'] = false
      }).catch(err => console.error(err))
  }
  cancelModalUpdateInfo() {
    this.modalCustomer['visible'] = false
    this.goBack()
  }
  search(value: any) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.customerShow = this.vhAlgorithm.searchList(val, this.listCustomer, ['phone', 'name'])
    } else this.customerShow = [...this.listCustomer]
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce'])
  }
  gotoAffiliateProduct() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/affiliate-product'])
  }
  gotoOrderStatus() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/order-status'])
  }
  gotoSellingPrice() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/update-selling-price'])
  }
  gotoUpdateStock() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/update-stock'])
  }
  gotoReturnRefund() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/return-refund'])
  }
  gotoCashflow() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/cashflow'])
  }
  gotoControlExportGoods() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/export-goods'])
  }
  gotoControlIssueInvoice() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/issue-invoice'])
  }

  gotoControl() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/control'])
  }
  gotoProductOnShop() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/product-on-shop'])
  }
  gotoControlReport() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/report'])
  }

  gotoAutoSale() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/auto-sale'])
  }

  gotoIssueByTiktok() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/e-invoice-tiktok'])
  }

  gotoIssueBySales365() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/e-invoice-sales365'])

  }
}
